import { Box, Chip, Grid, Typography } from '@mui/material';
import React, { useState } from 'react';
import { useSearchParams } from 'react-router-dom';
import LoadingButton, {
  LoadingButtonState,
} from '../../../components/LoadingButton';
import { addItemToCart } from '../../../redux/reducers/productSlice';
import { useAppDispatch, useAppSelector } from '../../../redux/redux-hooks';

const Product = () => {
  const [priceSelected, setPriceSelected] = useState<number>(0);
  const [state, setState] = useState<LoadingButtonState>('idle');

  const [getSearchParams] = useSearchParams();

  const dispatch = useAppDispatch();

  const id = getSearchParams.get('id');
  const product = useAppSelector((state) =>
    state.productReducer.products.find((product) => product.id === id)
  );

  const price = (
    (product?.prices[priceSelected].unit_amount || 0) / 100
  ).toFixed(2);

  const handleAddToCart = () => {
    setState('loading');
    setTimeout(() => {
      const productId = product?.id!;
      const priceId = product?.prices[priceSelected].id!;
      dispatch(addItemToCart({ productId, priceId }));
      setState('success');
      setTimeout(() => {
        setState('idle');
      }, 800);
    }, 800);
  };

  return (
    <Grid container justifyContent={'center'} m={2}>
      {!!product?
        <>
          <Grid item xs={10} sm={10} md={9} lg={10}>
            <Typography
              textAlign={'center'}
              variant="h4"
              color={(t) => t.palette.primary.main}
            >
              {product?.name}
            </Typography>
            <Box my={3} mx={2}>
              <Typography
                textAlign={'justify'}
                color={(t) => t.palette.grey[600]}
              >
                {product?.description}
              </Typography>
              <Box mt={3}>
                <Box display={'flex'} flexWrap="wrap" alignItems="center">
                  <Typography mr={5}>
                    Taglio selezionato:{' '}
                    <strong>{product?.prices[priceSelected].nickname}</strong>
                  </Typography>
                  <Typography
                    variant="body1"
                    color={(t) => t.palette.primary.light}
                  >
                    € {price}
                  </Typography>
                </Box>

                <Box display={'flex'} justifyContent={'start'} my={2}>
                  {product?.prices.map((price, index) => (
                    <Chip
                      key={price.id}
                      sx={{
                        marginX: 1,
                        border: '1px solid gray',
                        borderColor:
                          index === priceSelected ? 'gray' : 'transparent',
                        transition: 'transform .15s ',
                        fontWeight: index === priceSelected ? 'bold' : '',
                        backgroundColor:
                          index === priceSelected ? 'lightgray' : '#eeeee4',
                        ':hover': {
                          borderColor: 'gray',
                          // backgroundColor: (t) => t.palette.action.hover,
                          backgroundColor: 'lightgray',
                          transform: 'scale(1.1)',
                        },
                      }}
                      label={price.nickname}
                      onClick={() => setPriceSelected(index)}
                    />
                  ))}
                </Box>

                <Box>
                  <Box
                    sx={{
                      width: { xs: '100%', md: '60%' },
                      margin: 'auto',
                    }}
                  >
                    <LoadingButton state={state} onClick={handleAddToCart}>
                      Aggiungi al carrello
                    </LoadingButton>
                  </Box>
                </Box>
              </Box>
            </Box>
          </Grid>
          <Grid item xs={8} sm={8} md={5} lg={3}>
            <img
              style={{ borderRadius: '10%' }}
              alt={product?.name}
              src={product?.images[0]}
              width="100%"
            />
          </Grid>
        </>
        :<Typography>Prodotto non trovato</Typography>
      }
    </Grid>
  );
};

export default Product;
