import React from 'react';

import { Button, CircularProgress } from '@mui/material';

import DoneIcon from '@mui/icons-material/Done';

export type LoadingButtonState = 'idle' | 'loading' | 'success' |'disabled';

type Props = {
  children?: React.ReactNode;
  type?: 'button' | 'submit' | 'reset';
   state?:LoadingButtonState
  onClick?: React.MouseEventHandler<HTMLButtonElement>;
  fullWidth?:boolean,
  width?:number
};

const LoadingButton = ({ children, type,onClick,state='idle',fullWidth=true,width }: Props) => {

  const backgroundColor = state === 'success' ? '#4bb543 !important' : '';
  return (
    <Button
      disabled={state==='loading'|| state==='disabled'}
      onClick={onClick}
      fullWidth={fullWidth}
    
      type={type}
      variant="contained"
      sx={{ mt: 3, mb: 2,backgroundColor,width:width}}
    >
       {(state === 'idle'||state==='disabled') &&  children}
      {state === 'loading' && <CircularProgress /> }
      {state === 'success' &&  <DoneIcon fontSize='large'/>}
    </Button>
  );
};

export default LoadingButton;
