import React, { useEffect, useState } from 'react';
import { Alert, Box, CircularProgress, Container } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { useAppDispatch } from '../../../redux/redux-hooks';
import { logout } from '../../../redux/reducers/customerSlice';
import { logoutLocalStorage } from '../../../helper/customer';

const Logout = () => {
  const [isLoggedOut, setIsLogout] = useState(false);
const navigate =useNavigate();
  const dispatch = useAppDispatch();

  useEffect(() => {
    setTimeout(() => {
      dispatch(logout());
      setIsLogout(true);
      logoutLocalStorage();
      setTimeout(() => {
          navigate('/')
      }, 500);
    }, 1000);
  }, []);

  return (
    <Container maxWidth="sm">
      <Alert color={isLoggedOut ? 'success' : 'info'} icon={false}>
        <Box display={'flex'} alignItems="center" gap={10}>
          {isLoggedOut ? 'Logout eseguito con successo' : 'Logout in corso'}
          {!isLoggedOut && <CircularProgress />}
        </Box>
      </Alert>
    </Container>
  );
};

export default Logout;
