import { Chip, Divider, Grid, Typography } from '@mui/material';

import React from 'react';
import {
  addItemToCart,
  CartItem,
  removeItemFromCart,
} from '../../redux/reducers/productSlice';
import { useAppDispatch } from '../../redux/redux-hooks';
import QuantityButton from '../../components/QuantityButton';
import RemoveButton from '../../components/RemoveButton';

type Props = {
  cartItem: CartItem;
};

const CartItemView = ({ cartItem }: Props) => {
  const price = (cartItem.price.unit_amount / 100).toFixed(2);

  const dispatch = useAppDispatch();

  const addListener = () => {
    dispatch(
      addItemToCart({
        productId: cartItem.productId,
        priceId: cartItem.price.id,
      })
    );
  };

  const subListener = () => {
    dispatch(
      removeItemFromCart({
        priceId: cartItem.price.id,
      })
    );
  };

  const removeListener=()=>{
    dispatch(removeItemFromCart({
      priceId:cartItem.price.id,
      clear: true
    }))
  }

  return (
    <>
      <Divider />
      <Grid container item display={'flex'} alignItems="stretch">
        <Grid item xs={4} sm={2}>
          <img width={'100%'} src={cartItem.image} alt={cartItem.name} />
        </Grid>

        <Grid container item p={1} xs={8} sm={10}>
          <Grid item xs={8}>
            <Typography variant="h5" color="gray">
              {cartItem.name}
            </Typography>
          </Grid>
          <Grid item textAlign={'end'} xs={4}>
            <Typography fontWeight={'bold'} variant="h6">
              {price} €
            </Typography>
          </Grid>

          <Grid
            container
            item
            display={'flex'}
            justifyContent="space-between"
            alignItems={'end'}
          >
            <Grid item container display={'flex'} alignItems='center'>
              <Grid xs={2} item>
                <Chip
                  sx={{
                    marginX: 1,
                    border: '1px solid gray',
                    borderColor: 'gray',
                    backgroundColor: 'lightgray',
                  }}
                  label={cartItem.price.nickname}
                />
              </Grid>
              <Grid
                xs={10}
                item
                container
                display={'flex'}
                justifyContent="flex-end"
                alignItems={'center'}
              >
                <Grid pr={2} item>
                  <RemoveButton onClick={removeListener} />
                </Grid>
                <Grid item>
                  <QuantityButton
                    addListener={addListener}
                    subListener={subListener}
                    value={cartItem.quantity}
                  />
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
      <Divider />
    </>
  );
};

export default CartItemView;
