import { CartItem, Product } from './../redux/reducers/productSlice';

const CART_KEY = 'cart';

export const saveCartInLocalStorage = (cart: CartItem[]) => {
  const data = JSON.stringify(cart);

  localStorage.setItem(CART_KEY, data);
};

export const loadCartFromLocalStorage = () => {
  const data = localStorage.getItem(CART_KEY) || '[]';

  const cart = JSON.parse(data) as CartItem[];

  return cart;
};


/**
 * 
 * @param cart contains all the products (prices) saved in cart
 * @param products fetched from Backend.
 * @returns only products in cart that exist inside fetched products (only the active ones are fetched)
 */
export const onlyValidPrices = (
  cart: CartItem[],
  products: Product[]
): CartItem[] => {
  let validPricesId = [] as string[];

  for (let product of products) {
    for (let price of product.prices) {
      validPricesId.push(price.id);
    }
  }

  return cart.filter((item) => validPricesId.includes(item.price.id));
};
