import React, { useEffect, useState } from 'react';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';
import Container from '@mui/material/Container';
import LoadingButton, {
  LoadingButtonState,
} from '../../../components/LoadingButton';
import { Box, CircularProgress } from '@mui/material';
import myAxios from '../../../config/myAxios';
import { getTokenFromLocalStorage } from '../../../helper/customer';
import { useNavigate } from 'react-router-dom';



export type Shipping = {
  name?: string;
  phone?: string;
  address?: {
    city?: string;
    country?: string;
    line1?: string;
    line2?: string;
    postal_code?: string;
    state?: string;
  };
};

export default function Address() {
  const [buttonState, setButtonState] = useState<LoadingButtonState>('idle');
  const [downloadingData, setDownloadingData] = useState(true);

  const navigate = useNavigate()

  const [name, setName] = useState<string | undefined>('');
  const [phone, setPhone] = useState<string | undefined>('');
  const [city, setCity] = useState<string | undefined>('');
  const [country] = useState<string | undefined>('IT');
  const [line1, setLine1] = useState<string | undefined>('');
  const [line2, setLine2] = useState<string | undefined>('');
  const [postal_code, setPostal_code] = useState<string | undefined>('');
  const [state, setState] = useState<string | undefined>('');

  useEffect(() => {
    const token = getTokenFromLocalStorage();

    setDownloadingData(true)
    myAxios
      .get<Shipping>('/customer/address', {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((resp) => {
        setDownloadingData(false);
        const shipping = resp.data;
        setName(shipping.name);
        setPhone(shipping.phone);
        setLine1(shipping.address?.line1);
        setLine2(shipping.address?.line2);
        setCity(shipping.address?.city);
        setState(shipping.address?.state);
        setPostal_code(shipping.address?.postal_code);
      })
      .catch((err) => {
        console.log(err);
        setDownloadingData(false);
      });
  }, []);

  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();

    const data: Shipping = { name, phone, address: {} };

    data.address = { city, country, line1, line2, postal_code, state };

    const token = getTokenFromLocalStorage();
    setButtonState('loading');
    myAxios
      .post('/customer/address', data, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((resp) => {
        setButtonState('idle');
        navigate(-1)
      })
      .catch((err) => {
        setButtonState('idle');
      });
  };
  return (
    <>
      <Container sx={{pb:5}} maxWidth="sm">
        {downloadingData ? (
          <Box
            height={'30vh'}
            display="flex"
            justifyContent={'center'}
            alignItems="center"
          >
            <CircularProgress size={50} />
          </Box>
        ) : (
          <Box component="form" onSubmit={handleSubmit}>
            <Typography variant="h4" color={'gray'}>
              Informazioni di spedizione
            </Typography>
            <Grid container spacing={3}>
              <Grid item xs={12} sm={6}>
                <TextField
                  required
                  id="name"
                  name="name"
                  label="Nome e cognome"
                  fullWidth
                  autoComplete="given-name"
                  variant="standard"
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField
                  required
                  id="phone"
                  name="phone"
                  label="Telefono"
                  type={'tel'}
                  fullWidth
                  autoComplete="tel"
                  variant="standard"
                  value={phone}
                  onChange={(e) => setPhone(e.target.value)}
                />
              </Grid>

              <Grid item xs={12}>
                <TextField
                  required
                  id="line1"
                  name="line1"
                  label="Riga indirizzo 1"
                  fullWidth
                  autoComplete="shipping address-line1"
                  variant="standard"
                  value={line1}
                  onChange={(e) => setLine1(e.target.value)}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  id="line2"
                  name="line2"
                  label="Riga indirizzo 2"
                  fullWidth
                  autoComplete="shipping address-line2"
                  variant="standard"
                  value={line2}
                  onChange={(e) => setLine2(e.target.value)}
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField
                  required
                  id="city"
                  name="city"
                  label="Città"
                  fullWidth
                  autoComplete="shipping address-level2"
                  variant="standard"
                  value={city}
                  onChange={(e) => setCity(e.target.value)}
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField
                  id="state"
                  name="state"
                  label="Provincia"
                  fullWidth
                  variant="standard"
                  value={state}
                  onChange={(e) => setState(e.target.value)}
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField
                  required
                  id="postal_code"
                  name="postal_code"
                  label="Codice postale"
                  fullWidth
                  autoComplete="shipping postal-code"
                  variant="standard"
                  type="text"
                  value={postal_code}
                  onChange={(e) => setPostal_code(e.target.value)}
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField
                  disabled={true}
                  required
                  id="country"
                  name="country"
                  label="stato"
                  fullWidth
                  autoComplete="shipping country"
                  variant="standard"
                  value={country}
                />
              </Grid>
            </Grid>
            <Box display={'flex'} justifyContent="end">
              <LoadingButton
                state={buttonState}
                type="submit"
                fullWidth={false}
                width={200}
              >
              Aggiorna
              </LoadingButton>
            </Box>
          </Box>
        )}
      </Container>
    </>
  );
}
