import React, { useEffect, useState } from 'react';

import myAxios from '../../config/myAxios';
import { Shipping } from '../account/address/Address';
import { Box, Button, CircularProgress, Grid, Typography } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import LoadingButton, {
  LoadingButtonState,
} from '../../components/LoadingButton';
import { CartItem } from '../../redux/reducers/productSlice';
import { useAppSelector } from '../../redux/redux-hooks';

type Props = {
  cart: CartItem[];
  addressLoading: boolean;
  shipping?: Shipping;
};
const AddressInfoAndButton = ({ cart, addressLoading, shipping }: Props) => {
  const customer = useAppSelector(
    (state) => state.customerReducer.customer.stripeID
  );

  const navigate = useNavigate();
  const [name, setName] = useState<string | undefined>('');
  const [phone, setPhone] = useState<string | undefined>('');
  const [city, setCity] = useState<string | undefined>('');
  const [line1, setLine1] = useState<string | undefined>('');
  const [postal_code, setPostal_code] = useState<string | undefined>('');
  const [state, setState] = useState<string | undefined>('');
  // const [country] = useState<string | undefined>('IT');
  // const [line2, setLine2] = useState<string | undefined>('');

  const [addressValid, setAddressValid] = useState(false);

  const [buttonState, setButtonState] = useState<LoadingButtonState>('idle');

  const validate = (shipping: Shipping) => {
    if (!shipping.name) return setAddressValid(false);
    if (!shipping.phone) return setAddressValid(false);
    if (!shipping.address?.line1) return setAddressValid(false);
    if (!shipping.address?.city) return setAddressValid(false);
    if (!shipping.address?.state) return setAddressValid(false);
    if (!shipping.address?.postal_code) return setAddressValid(false);

    setAddressValid(true);
  };

  const checkout = () => {
    setButtonState('loading');
    const itemList = cart.map((item) => ({
      quantity: item.quantity,
      price: item.price.id,
    }));

    myAxios
      .post('/stripe/payment', { customer, itemList })
      .then((resp) => {
        window.location.replace(resp.data.checkoutUrl);
        setButtonState('idle');
      })

      .catch((err) => {
        setButtonState('idle');
        console.log(err);
      });
  };

  useEffect(() => {
    if(!shipping)return
    validate(shipping);
    setName(shipping.name);
    setPhone(shipping.phone);
    setLine1(shipping.address?.line1);
    // setLine2(shipping.address?.line2);
    setCity(shipping.address?.city);
    setState(shipping.address?.state);
    setPostal_code(shipping.address?.postal_code);
  }, [shipping]);

  return (
    <>
      <Box display={addressLoading ? 'block' : 'none'} textAlign={'center'}>
        <CircularProgress />
      </Box>
      <Box display={addressLoading ? 'none' : 'inline'}>
        <Grid
          container
          pt={3}
          display={'flex'}
          alignItems="center"
          justifyContent="space-between"
        >
          <Grid item xs={12} md={6}>
            <Box
              display={'flex'}
              justifyContent="space-between"
              alignItems={'center'}
              gap="10px"
            >
              <Box>
                <Typography variant="h5">Dettagli spedizione</Typography>
                <Typography variant="body1">{name}</Typography>
                <Typography variant="body1">{phone}</Typography>
                <Typography variant="body1">{line1}</Typography>
                <Typography variant="body1">
                  {city}, {state} {postal_code}
                </Typography>
              </Box>
              <Button
                variant={addressValid ? 'text' : 'contained'}
                size={addressValid ? 'medium' : 'small'}
                onClick={() => navigate('/account/address')}
                color={addressValid ? 'primary' : 'warning'}
              >
                {addressValid
                  ? 'Aggiorna dati'
                  : 'Inserisci indirizzo spedizione'}
              </Button>
            </Box>
          </Grid>
          <Grid item xs={12} md={3} textAlign="center">
            <LoadingButton
              onClick={checkout}
              fullWidth={false}
              width={150}
              state={
                cart.length === 0 || !addressValid ? 'disabled' : buttonState
              }
            >
              Procedi con l'ordine
            </LoadingButton>
          </Grid>
        </Grid>
      </Box>
    </>
  );
};
export default AddressInfoAndButton;
