import React from 'react';
import { Badge, IconButton, Stack, Typography } from '@mui/material';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCartOutlined';
import { useAppSelector } from '../../redux/redux-hooks';
import { useNavigate } from 'react-router-dom';

const CartButton = () => {

  const items=useAppSelector(state=>state.productReducer.cart.reduce((prev,current)=>prev+current.quantity,0))
  const total=(useAppSelector(state=>state.productReducer.cart.reduce((prev,current)=>prev+current.price.unit_amount*current.quantity,0))/100).toFixed(2)

  const navigate=useNavigate()

  return (
    <Stack
      onClick={() => navigate('/Cart')}
      sx={{
        pt: 1,
        ':hover': {
          backgroundColor: (t) => t.palette.action.selected,
          cursor: 'pointer',
        },
      }}
    >
      <IconButton disableRipple={true} sx={{ m: 0, p: 0 }}>
        <Badge badgeContent={items} color="primary">
          <ShoppingCartIcon sx={{ fontSize: { xs: '1.5rem', md: '2rem' } }} />
        </Badge>
      </IconButton>
      <Typography
        textAlign={'center'}
        sx={{ textDecoration: 'underline' }}
        color={'gray'}
        variant="subtitle1"
      >
        {`${total} €`}
      </Typography>
    </Stack>
  );
};

export default CartButton;
