import {
  Box,
  
  
  
  
  Typography,
} from '@mui/material';
import React from 'react';
import { useNavigate } from 'react-router-dom';
import LoadingButton from '../../../components/LoadingButton';
import { useAppSelector } from '../../../redux/redux-hooks';

const SideCart = () => {
  const cart = useAppSelector((state) => state.productReducer.cart);

  const navigate = useNavigate()
  
  const total= (cart.reduce((subTot,item)=>{return (item.price.unit_amount*item.quantity+subTot)},0)/100).toFixed(2)
  return (
    <Box>
      <Box textAlign={'center'}>
        <Typography>
          Subtotale: <strong>{total}€</strong>
        </Typography>
        <LoadingButton
          onClick={() => navigate('/cart')}
          state={cart.length === 0 ? 'disabled' : 'idle'}
          fullWidth={false}
        >
          Vai al carrello
        </LoadingButton>
      </Box>
      {cart.map((item, index) => (
        <Box key={index} py={1}>
          <Typography variant="body1" component="div">
            {item.name}
          </Typography>
          <Box display={'flex'} gap={2}>
            <Typography color="text.secondary">
              {item.price.nickname}
            </Typography>
            <Typography>x{item.quantity}</Typography>
          </Box>
        </Box>
      ))}
    </Box>
  );
};

export default SideCart;
