import { Box, CircularProgress, Container, Typography } from '@mui/material';
import React, { useEffect, useState } from 'react';

import myAxios from '../../../config/myAxios';
import { getTokenFromLocalStorage } from '../../../helper/customer';
import { OrderI } from '../../../helper/order';
import OrderListItem from './OrderListItem';

const OrderList = () => {
  const [orderList, setOrderList] = useState<OrderI[] | null>(null);
  const [loading, setLoading] = useState<boolean>(true);

  useEffect(() => {
    setLoading(true);
    const token = getTokenFromLocalStorage();
    myAxios
      .get<OrderI[]>('/customer/orders', {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then(({ data }) => {
        setLoading(false);
        return setOrderList(data);
      })
      .catch((err) => {
        setLoading(false);
        console.log(err);
      });
  }, []);

  return (
    <Container maxWidth="md">
      {loading && (
        <Box textAlign={'center'}>
          <CircularProgress />
        </Box>
      )}
      {orderList?.length === 0 && (
        <Typography variant="h5">Nessun ordine</Typography>
      )}
      {orderList?.map((order, index) => (
        <OrderListItem key={index} order={order} />
      ))}
    </Container>
  );
};

export default OrderList;
