import {
  Box,
  Button,
  CircularProgress,
  Container,
  Divider,
  Grid,
  Typography,
} from '@mui/material';
import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';

import myAxios from '../../../config/myAxios';
import { getTokenFromLocalStorage } from '../../../helper/customer';
import { LineItemI, OrderI } from '../../../helper/order';
import { Shipping } from '../address/Address';

const OrderItemDetails = () => {
  const { orderId } = useParams();

  const token = getTokenFromLocalStorage();

  const [loading, setLoading] = useState<boolean>(true);

  const [orderNum, setOrderNum] = useState<string>('');
  const [shipping, setShipping] = useState<Shipping | null>(null);
  const [lineItems, setLineItems] = useState<LineItemI[] | null>(null);
  const [shippingCost, setShippingCost] = useState<number>(0);
  const [trackingCode, setTrackingCode] = useState<string>('');
  const [amount, setAmount] = useState<number>(0);
  const [receipt, setReceipt] = useState<string | null>(null);

  useEffect(() => {
    setLoading(true);
    myAxios
      .get<{ order: OrderI; lineItems: LineItemI[] }>(
        `/customer/orders/${orderId}`,
        {
          headers: { authorization: `Bearer ${token}` },
        }
      )
      .then(({ data }) => {
        setLoading(false);
        setShipping(data.order.shipping);
        setLineItems(data.lineItems);
        setOrderNum(data.order.orderNum);
        setShippingCost(data.order.shippingCost);
        setTrackingCode(data.order.trackingCode);
        setAmount(data.order.amount);
        setReceipt(data.order.receipt);
        console.log(data.lineItems);
      })
      .catch((err) => {
        setLoading(false);
        console.log(err);
      });
  }, []);

  return (
    <Container maxWidth="md" sx={{ pt: 3 }}>
      <Box display={loading ? 'block' : 'none'} textAlign={'center'}>
        <CircularProgress />
      </Box>
      <Box display={loading ? 'none' : 'block'}>
        <Box display={'flex'} justifyContent="center" alignItems={'baseline'}>
          <Typography textAlign={'center'} variant="h6">
            ORDINE #
          </Typography>
          <Typography pl={1} textAlign={'center'} variant="body1">
            {orderNum}
          </Typography>
        </Box>
        <Box py={4} display={'flex'} justifyContent="space-between">
          <Box>
            <Typography variant="h5">Dettagli spedizione</Typography>
            <Typography variant="body1">{shipping?.name}</Typography>
            <Typography variant="body1">{shipping?.phone}</Typography>
            <Typography variant="body1">{shipping?.address?.line1}</Typography>
            <Typography variant="body1">
              {shipping?.address?.city}, {shipping?.address?.state},{' '}
              {shipping?.address?.postal_code}
            </Typography>
          </Box>
          {receipt&&<Box>
            <Button
              onClick={() => {
                const newWindow = window.open(
                  receipt,
                  '_blank',
                  'noopener,noreferrer'
                );
                if (newWindow) newWindow.opener = null;
              }}
            >
              Ricevuta
            </Button>
          </Box>}
        </Box>
        {<Typography py={2}>Codice tracking: {trackingCode}</Typography>}
        {lineItems &&
          lineItems.map((item, ind) => (
            <Grid
              key={ind}
              m="auto"
              py={1}
              sx={{ borderBottom: 'solid 2px #16273814' }}
              container
            >
              <Grid xs={1} textAlign="center" item>
                <Typography>{item.quantity}x</Typography>
              </Grid>
              <Grid
                xs={6}
                display="flex"
                flexDirection={'column'}
                alignItems={'start'}
                item
              >
                <Typography>{item.description}</Typography>
                <Typography variant="body2" style={{ color: 'gray' }}>
                  {item.price.nickname}
                </Typography>
              </Grid>
              <Grid xs={2} item>
                <Typography sx={{ color: 'gray' }}>
                  € {(item.price.unit_amount / 100).toFixed(2)}
                </Typography>
              </Grid>
              <Grid xs={3} item>
                <Typography textAlign={'end'}>
                  € {(item.amount_total / 100).toFixed(2)}
                </Typography>
              </Grid>
            </Grid>
          ))}
        <Grid
          my={2}
          pb={2}
          sx={{ borderBottom: 'solid 4px #162738a0' }}
          container
          display={'flex'}
        >
          <Grid item xs={9} textAlign="center">
            Spese spedizione:
          </Grid>
          <Grid item xs={3}>
            <Typography textAlign={'end'}>
              € {(shippingCost / 100).toFixed(2)}
            </Typography>
          </Grid>
        </Grid>
        <Grid container display={'flex'}>
          <Grid item xs={8} textAlign="end">
            <Typography variant="h5">Totale:</Typography>
          </Grid>
          <Grid item xs={4}>
            <Typography variant="h5" textAlign={'end'}>
              € {(amount / 100).toFixed(2)}
            </Typography>
          </Grid>
        </Grid>
      </Box>
    </Container>
  );
};

export default OrderItemDetails;
