import React, { useState } from 'react';
import {
  Avatar,
  Box,
  Container,
  Grid,
  Link,
  TextField,
  Typography,
  Alert,
  AlertTitle,
} from '@mui/material';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import { useNavigate } from 'react-router-dom';

import myAxios from '../../config/myAxios';
import { getCustomerFromToken, saveTokenToLocalStorage } from '../../helper/customer';
import LoadingButton, { LoadingButtonState } from '../../components/LoadingButton';
import { useAppDispatch } from '../../redux/redux-hooks';
import { setCustomer } from '../../redux/reducers/customerSlice';

const Login = () => {
  const navigate = useNavigate();
  const dispatch = useAppDispatch();

  const [state,setState]=useState<LoadingButtonState>('idle')
   const [errorCode, setErrorCode] = useState<number | null>(null);

  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    const formData = new FormData(event.currentTarget);

    const email = formData.get('email')?.toString();
    const password = formData.get('password')?.toString();

    if (email?.length === 0 || password?.length === 0)
      return console.log('Compila i campi');

    
setState('loading')
    myAxios
      .post<{ token: string }>('/customer/login', {
        email,
        password,
      })
      .then((resp) => {
        const token = resp.data.token;

        setState('idle');
        saveTokenToLocalStorage(token);
        const customer = getCustomerFromToken(token);

        if (customer) dispatch(setCustomer(customer));
        navigate('/Account');
      })
      .catch((err) => {
        setState('idle');
        setErrorCode(err.response.status);
      });
  };

  const wrongDataErrorMessage = (
    <Alert variant="standard" severity="error">
      <AlertTitle>Credenziali errate</AlertTitle>
      Assicurati di avere inserito i dati corretti e riprova
    </Alert>
  );
  const emailConfirmErrorMessage = (
    <Alert variant="standard" severity="error">
      <AlertTitle>Account non confermato</AlertTitle>
      Controlla il tuo indirizzo email e verifica il tuo account
    </Alert>
  );

  return (
    <Container maxWidth="xs">
      <Box sx={{ width: '90%', m: 'auto' }}>
        <Box
          sx={{
            marginTop: 8,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <Avatar
            sx={{ m: 1, bgcolor: (theme) => theme.palette.secondary.main }}
          >
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Login
          </Typography>

          <Box
            component="form"
            autoComplete=""
            onSubmit={handleSubmit}
            noValidate
            sx={{ mt: 1 }}
          >
            <TextField
              margin="normal"
              required
              fullWidth
              id="email"
              label="Email Address"
              name="email"
              autoComplete="email"
              type="email"
              autoFocus
            />
            <TextField
              margin="normal"
              required
              fullWidth
              name="password"
              label="Password"
              type="password"
              id="password"
              autoComplete="new-password"
            />

            {errorCode === 401 && wrongDataErrorMessage}
            {errorCode === 403 && emailConfirmErrorMessage}

            <LoadingButton  state={state} type="submit">
              Login
            </LoadingButton>
          </Box>
          <Grid container>
            <Grid item xs>
              <Link href="/recover" variant="body2">
                Password dimenticata?
              </Link>
            </Grid>
            <Grid item>
              <Link href="/signup" variant="body2">
                Non sei registrato? <b>Iscriviti</b>
              </Link>
            </Grid>
          </Grid>
        </Box>
      </Box>
    </Container>
  );
};

export default Login;
