import React from 'react';
import {
  Box,
  Divider,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
} from '@mui/material';

import { useNavigate, useLocation } from 'react-router-dom';

import AccountCircle from '@mui/icons-material/AccountCircleOutlined';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCartOutlined';
import Logo from '../header/Logo';
import {
  mealIconsByMealType,
  mealType,
  mealTypeArray,
} from '../../helper/foodData';
import { useAppSelector } from '../../redux/redux-hooks';

type Props = {
  closeDrawer?: () => void;
};

const DrawerContent = ({ closeDrawer }: Props) => {
  const navigate = useNavigate();
  const location = useLocation();
  const mealsArray = mealTypeArray();
  const menuSelected = location.pathname.split('/')[1];

  const customerName = useAppSelector((state) => state.customerReducer.customer.name);

  const isSelected = (mealType: string) => {
    const pathname = location.pathname;

    const lastSlash = pathname.lastIndexOf('/');
    const value = decodeURI( pathname.substring(lastSlash + 1));
    
console.log((value),mealType)
    return value === mealType;
  };

  const accountMessage = !!customerName ? `Ciao ${customerName}` : 'Accedi';

  const onClickHandler = (url: string) => {
    navigate(url);
    if (closeDrawer) closeDrawer();
  };

  return (
    <List disablePadding={true}>
      <Box textAlign={'center'}>
        <Logo />
      </Box>
      <ListItemButton
        onClick={() => onClickHandler('/Account')}
        selected={menuSelected === 'Account'}
      >
        <ListItemIcon>
          <AccountCircle />
        </ListItemIcon>
        <ListItemText primary="Account" secondary={accountMessage} />
      </ListItemButton>

      <ListItemButton
        onClick={() => onClickHandler('/Cart')}
        selected={menuSelected === 'Cart'}
      >
        <ListItemIcon>
          <ShoppingCartIcon />
        </ListItemIcon>
        <ListItemText primary="Carrello" />
      </ListItemButton>

      <Divider />
      <ListItem sx={{ height: '50px' }}>
        <ListItemText inset primary="Menu" />
      </ListItem>
      <Divider />

      {mealsArray.map((meal: mealType, index) => (
        <ListItemButton
          key={index}
          onClick={() => onClickHandler('/type/' + meal)}
          selected={isSelected(meal)}
        >
          <ListItemText primary={meal} />
          <ListItemIcon sx={{ display: 'inline', textAlign: 'center' }}>
            <img
              style={{ height: '1.8rem' }}
              alt={meal}
              src={mealIconsByMealType(meal)}
            />
          </ListItemIcon>
        </ListItemButton>
      ))}
    </List>
  );
};

export default DrawerContent;
