import {
  saveCartInLocalStorage,
  loadCartFromLocalStorage,
  onlyValidPrices,
} from './../../helper/cart';
import { mealType } from './../../helper/foodData';
import { createSlice, createAsyncThunk, PayloadAction } from '@reduxjs/toolkit';

import myAxios from '../../config/myAxios';

interface Price {
  id: string;
  unit_amount: number;
  nickname: string;
}

export interface CartItem {
  productId: string;
  price: Price;
  quantity: number;
  name: string;
  image: string;
}

export interface Product {
  id: string;
  description: string;
  type: mealType;
  name: string;
  images: string[];
  prices: Price[];
  metadata?: {
    type: mealType;
  };
}

const initialState = {
  products: [] as Product[],
  cart: [] as CartItem[],
  // cart: loadCartFromLocalStorage() as CartItem[],
};

export const fetchProducts = createAsyncThunk(
  'products/fetchProducts',
  async () => {
    return myAxios
      .get<Record<string, Product>>('product/getAll')
      .then((resp) => {
        const products = [] as Product[];
        for (let key in resp.data) {
          const product = resp.data[key];
          const { id, prices, name, images, description } = product;
          const type = product.metadata!.type!;
          prices.sort(
            (price1, price2) => price1.unit_amount - price2.unit_amount
          );
          products.push({ id, prices, name, type, images, description });
        }

        return products;
      })
      .catch((err) => [] as Product[]);
  }
);

export const productsSlice = createSlice({
  name: 'product',
  initialState,
  reducers: {
    addItemToCart(
      state,
      action: PayloadAction<{
        productId: string;
        priceId: string;
        amount?: number;
      }>
    ) {
      const { priceId, productId, amount = 1 } = action.payload;

      const productFound: Product = state.products.find(
        (product) => product.id === productId
      ) as Product;

      if(!productFound)
      return
      const price = productFound.prices.find(
        (price) => price.id === priceId
      )! as Price;

      const itemOnCart = state.cart.find((item) => item.price.id === priceId);

      if (itemOnCart) {
        itemOnCart.quantity += amount;
      } else {
        const cartItem: CartItem = {
          productId,
          name: productFound.name,
          image: productFound.images[0],
          price: {
            id: price.id,
            nickname: price.nickname,
            unit_amount: price.unit_amount,
          } as Price,
          quantity: 1,
        };

        state.cart.push(cartItem);
      }

      saveCartInLocalStorage(state.cart);
    },
    removeItemFromCart(
      state,
      action: PayloadAction<{
        priceId: string;
        clear?: boolean;
      }>
    ) {
      const { priceId, clear = false } = action.payload;

      const itemOnCartIndex = state.cart.findIndex(
        (item) => item.price.id === priceId
      );
      const itemOnCart = state.cart.at(itemOnCartIndex);

      if (itemOnCart) {
        if (clear || itemOnCart.quantity === 1)
          state.cart = state.cart.filter(
            (item, index) => index !== itemOnCartIndex
          );
        else itemOnCart.quantity -= 1;
      }

      saveCartInLocalStorage(state.cart);
    },
    clearCart(state) {
       state.cart = [];
      saveCartInLocalStorage(state.cart);
    },
  },
  extraReducers: (builder) => {
    builder.addCase(fetchProducts.fulfilled, (state, action) => {
      const products = action.payload as Product[];
      state.products = products;

      const oldCart = loadCartFromLocalStorage();

      state.cart=onlyValidPrices(oldCart,products)
      saveCartInLocalStorage(state.cart);
    });
  },
});

export default productsSlice.reducer;

export const { addItemToCart, removeItemFromCart, clearCart } =
  productsSlice.actions;
