import React from 'react';
import { Grid, Typography } from '@mui/material';
import moment from 'moment';
import { useNavigate } from 'react-router-dom';
import { OrderI, OrderStatus } from '../../../helper/order';

type Props = {
  order: OrderI;
};
const OrderListItem = ({ order }: Props) => {
 
const navigate = useNavigate()
  const statusColor = {
    [OrderStatus.IN_LAVORAZIONE as OrderStatus]: '#d3d3d35a',
    [OrderStatus.CONSEGNATO as OrderStatus]: '#1ed40d53',
  };
  const date = moment(order.createdAt).format('YYYY-MM-DD');
  const amount = (order.amount / 100).toFixed(2);
  
  return (
    <Grid
    onClick={()=>navigate(`${order._id}`)}
      my={2}
      p={1}
      sx={{
        borderRadius: 2,
        backgroundColor: statusColor[order.orderStatus],
        cursor: 'pointer',
        transition: 'all .15s ',
        boxShadow: '1px 1px #88888860',

        ':hover': { transform: 'scale(1.01)', boxShadow: '2px 2px #88888860' },
      }}
      container
      display={'flex'}
      justifyContent="space-between"
      
    >
      <Grid item xs={4} md={2} display="flex" flexDirection={'column'}>
        <Typography fontWeight={'bold'}>Data ordine:</Typography>
        <Typography fontWeight={'200'}>{date}</Typography>
      </Grid>
      <Grid item xs={4} md={2} display="flex" flexDirection={'column'}>
        <Typography fontWeight={'bold'}>Totale:</Typography>
        <Typography fontWeight={'200'}>{amount} €</Typography>
      </Grid>
      <Grid
        item
        md={3}
        display={{ md: 'flex', xs: 'none' }}
        flexDirection={'column'}
      >
        <Typography fontWeight={'bold'}>Order #</Typography>
        <Typography fontWeight={'200'}>{order.orderNum}</Typography>
      </Grid>
      <Grid
        item
        md={2}
        display={{ md: 'flex', xs: 'none' }}
        flexDirection={'column'}
      >
        <Typography fontWeight={'bold'}>Tracking code</Typography>
        <Typography fontWeight={'200'}>glsbartolini</Typography>
      </Grid>
      <Grid
        item
        xs={4}
        md={2}
        display={{ md: 'flex' }}
        flexDirection={'column'}
      >
        <Typography fontWeight={'bold'}>Stato</Typography>
        <Typography fontWeight={'200'}>{order.orderStatus}</Typography>
      </Grid>
    </Grid>
  );
};

export default OrderListItem;
