import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { Customer, getCustomerFromLocalStorage } from '../../helper/customer';

const initialState = {
 customer: {...(getCustomerFromLocalStorage() as Customer)},
};

const userSlice = createSlice({
  name: 'customer',
  initialState,
  reducers: {
    setCustomer(state, action: PayloadAction<Customer>) {
      const {email,name,stripeID} = action.payload
      state.customer.email=email
      state.customer.name = name;
      state.customer.stripeID = stripeID;
    },
    logout(state){
      state.customer={name:'',email:'',stripeID:''}
    }
  },
});

export const {setCustomer,logout} =userSlice.actions;
export default userSlice.reducer;
