import { Alert, AlertTitle, Container } from '@mui/material';
import React, { useEffect, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';

import myaxios from '../../config/myAxios';

import { getCustomerFromToken } from '../../helper/customer';

const ConfirmPage = () => {
  const [success, setSuccess] = useState<'ok' | 'loading' | 'ko'>('loading');
  const { token } = useParams<{ token: string }>();
  const navigate = useNavigate();
  const customer = getCustomerFromToken(token);

  useEffect(() => {
    if (success === 'ok')
      setTimeout(() => {
        navigate('/login');
      }, 4000);
  }, [success, navigate]);

  useEffect(() => {
    setTimeout(() => {
      myaxios
        .get('/customer/confirm/' + token)
        .then((resp) => {
          setSuccess('ok');
        })
        .catch((err) => {
          setSuccess('ko');
          console.log(err);
        });
    }, 2000);
  }, [token]);

  const infoMessage = (
    <Alert sx={{ mt: 10, fontSize: '1rem' }} severity="info">
      <AlertTitle>Attendi..</AlertTitle>
      Conferma account in corso..
    </Alert>
  );

  const successMessage = (
    <Alert sx={{ mt: 10, fontSize: '1rem' }} severity="success">
      <AlertTitle>Account confermato</AlertTitle>
      Grazie <strong>{customer?.name}</strong>
      <br /> Account confermato correttamente
    </Alert>
  );
  const ErrorMessage = (
    <Alert sx={{ mt: 10, fontSize: '1rem' }} severity="error">
      <AlertTitle>Errore</AlertTitle>
      Impossibile confermare l'account.
    </Alert>
  );

  return (
    <Container maxWidth="xs">
      {success === 'ok' && successMessage}
      {success === 'loading' && infoMessage}
      {success === 'ko' && ErrorMessage}
    </Container>
  );
};

export default ConfirmPage;
