import React, { useEffect, useState } from 'react';
import {
  Alert,
  AlertTitle,
  Avatar,
  Box,
  Container,
  Link,
  TextField,
  Typography,
} from '@mui/material';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import { useNavigate } from 'react-router-dom';

import myAxios from '../../config/myAxios';
import LoadingButton, {
  LoadingButtonState,
} from '../../components/LoadingButton';

const SignUp = () => {
  const navigate = useNavigate();

  const [state, setState] = useState<LoadingButtonState>('idle');

  const [nameError, setNameError] = useState<string | null>(null);
  const [emailError, setEmailError] = useState<string | null>(null);
  const [passwordError, setpasswordError] = useState<string | null>(null);
  const [password2Error, setpassword2Error] = useState<string | null>(null);

  useEffect(() => {
    let timeout: any;
    if (state === 'success')
      timeout = setTimeout(() => {
        navigate('/login');
      }, 5000);

    return () => {
      clearTimeout(timeout);
    };
  }, [state, navigate]);

  const clearError = () => {
    setNameError(null);
    setEmailError(null);
    setpasswordError(null);
    setpassword2Error(null);
  };
  const validate = (
    name: string,
    email: string,
    password: string,
    password2: string
  ) => {
    let validate = true;

    if (name.length < 3) {
      setNameError('Il nome deve contenere almeno 3 caratteri');
      validate = false;
    }

    if (email?.length < 5) {
      setEmailError('Inserire una mail valida');
      validate = false;
    }

    if (password?.length === 0) {
      setpasswordError('La password deve contenere almeno 6 caratteri');
      validate = false;
    }

    if (password2?.length === 0) {
      setpassword2Error('La password deve contenere almeno 6 caratteri');
      validate = false;
    }

    if (password !== password2) {
      setpasswordError('Le password devono coincidere');
      setpassword2Error('Le password devono coincidere');
      validate = false;
    }

    return validate;
  };

  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    const formData = new FormData(event.currentTarget);

    const name = formData.get('name')?.toString() || '';
    const email = formData.get('email')?.toString() || '';
    const password = formData.get('password')?.toString() || '';
    const password2 = formData.get('password2')?.toString() || '';

    if (!validate(name, email, password, password2)) return;

    setState('loading');
    myAxios
      .post('/customer/signup', {
        email,
        password,
        name,
      })
      .then((resp) => {
        setState('success');
      })
      .catch((err) => {
        if (err.response.status === 409) setEmailError('Email già in uso');

        setState('idle');
      });
  };

  const signUpSuccess = (
    <Alert sx={{ mt: 10, fontSize: '1rem' }} severity="success">
      <AlertTitle>Registrazione avvenuta</AlertTitle>
      Verifica la tua mail per confermare l'account
    </Alert>
  );

  const signupForm = (
    <>
      <Box sx={{ width: '90%', m: 'auto' }}>
        <Box
          sx={{
            marginTop: 8,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <Avatar
            sx={{ m: 1, bgcolor: (theme) => theme.palette.secondary.main }}
          >
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Iscriviti
          </Typography>

          <Box
            component="form"
            autoComplete=""
            onSubmit={handleSubmit}
            noValidate
            sx={{ mt: 1 }}
          >
            <TextField
              margin="normal"
              required
              fullWidth
              id="name"
              label="Nome"
              name="name"
              type="text"
              autoFocus
              error={!!nameError}
              helperText={nameError}
              onChange={() => clearError()}
            />
            <TextField
              margin="normal"
              required
              fullWidth
              id="email"
              label="Email Address"
              name="email"
              type="email"
              autoFocus
              error={!!emailError}
              helperText={emailError}
              onChange={() => clearError()}
            />
            <TextField
              margin="normal"
              required
              fullWidth
              name="password"
              label="Password"
              type="password"
              id="password"
              error={!!passwordError}
              helperText={passwordError}
              onChange={() => clearError()}
            />
            <TextField
              margin="normal"
              required
              fullWidth
              name="password2"
              label="Conferma password"
              type="password"
              id="password2"
              error={!!password2Error}
              helperText={password2Error}
              onChange={() => clearError()}
            />

            <LoadingButton state={state} type="submit">
              Iscriviti
            </LoadingButton>
          </Box>
          <Box>
            <Link href="/login" variant="body2">
              Già registrato? <b>Accedi</b>
            </Link>
          </Box>
        </Box>
      </Box>
    </>
  );

  return (
    <Container maxWidth="xs">
      {state === 'success' ? signUpSuccess : signupForm}
    </Container>
  );
};
export default SignUp;
