import React from 'react';
import { Navigate, Route, Routes } from 'react-router-dom';
import Account from '../pagination/account/Account';
import Address from '../pagination/account/address/Address';
import ConfirmPage from '../pagination/account/ConfirmPage';
import Login from '../pagination/account/Login';
import Logout from '../pagination/account/logout/Logout';
import OrderItemDetails from '../pagination/account/order/OrderItemDetails';
import OrderList from '../pagination/account/order/OrderList';
import SignUp from '../pagination/account/SignUp';
import OrderSuccess from '../pagination/account/success/OrderSuccess';
import Index from '../pagination/admin/Index';
import Cart from '../pagination/cart/Cart';
import Content from '../pagination/shopping/content/Content';
import RequireAdmin from './RequireAdmin';

import RequireAuth from './RequireAuth';

const MyRoutes = () => {
  return (
    <Routes>
      <Route path="/confirm/:token" element={<ConfirmPage />} />
      <Route path="/login" element={<Login />} />
      <Route path="/signup" element={<SignUp />} />

      <Route element={<RequireAuth />}>
        <Route path="/account">
          <Route index element={<Account />} />
          <Route path="address" element={<Address />} />
          <Route path="logout" element={<Logout />} />
          <Route path="success" element={<OrderSuccess />} />
          <Route path="orders" element={<OrderList />} />
          <Route path="orders/:orderId" element={<OrderItemDetails />} />
        </Route>
        <Route path="check/address" element={<Address />} />
      </Route>

      

      <Route path="/cart" element={<Cart />} />

      <Route element={<RequireAdmin />}>
        <Route path="/admin" element={<Index />} />
      </Route>

      <Route path="/type/:type" element={<Content />} />

      <Route path="*" element={<Navigate to={'/type/Pollame'} />} />
    </Routes>
  );
};

export default MyRoutes;
