import pollameIcon from '../assets/pollame.svg';
import bovinoIcon from '../assets/bovino.svg';
import suinoIcon from '../assets/suino.svg';
import ovinoIcon from '../assets/ovino.svg';
import equinoIcon from '../assets/equino.svg';
import cheeseIcon from '../assets/cheese.svg';
import jarIcon from '../assets/jar.svg';
import pastaSeccaIcon from '../assets/pasta_secca.svg';
import pastaFrescaIcon from '../assets/pasta_fresca.png';

export enum mealType {
  POLLAME = 'Pollame',
  OVINO = 'Ovino',
  SUINO = 'Suino',
  BOVINO = 'Bovino',
  EQUINO = 'Equino',
  FORMAGGI_SALUMI = 'Formaggi e salumi',
  PASTA_TIPICA = 'Pasta tipica',
  PASTA_FRESCA = 'Pasta fresca',
  SOTTOLIO = 'Sottolio',
}

export const mealTypeArray = (): Array<mealType> => {
  const values = Object.values(mealType);
  return values;
};

export const mealIconsByMealType = (meal: mealType) => {
  switch (meal) {
    case mealType.POLLAME:
      return pollameIcon;
    case mealType.OVINO:
      return ovinoIcon;
    case mealType.SUINO:
      return suinoIcon;
    case mealType.BOVINO:
      return bovinoIcon;
    case mealType.EQUINO:
      return equinoIcon;

    case mealType.FORMAGGI_SALUMI:
      return cheeseIcon;

    case mealType.PASTA_FRESCA:
      return pastaFrescaIcon;

    case mealType.PASTA_TIPICA:
      return pastaSeccaIcon;
    case mealType.SOTTOLIO:
      return jarIcon;

    default:
      return ovinoIcon;
  }
};
