import React from 'react';
import { Box, Container, Stack, Typography } from '@mui/material';

import ManageAccountsIcon from '@mui/icons-material/ManageAccounts';
import HomeIcon from '@mui/icons-material/Home';
import FormatListBulletedIcon from '@mui/icons-material/FormatListBulleted';
import LogoutIcon from '@mui/icons-material/Logout';
import AdminIcon from '@mui/icons-material/AdminPanelSettingsOutlined';

import { useNavigate } from 'react-router-dom';
import { isAdmin } from '../../helper/customer';

const Account = () => {
  const navigate = useNavigate();
  const accountInfo = [
    {
      name: 'Ordini',
      Icon: FormatListBulletedIcon,
      onClick: () => {
        navigate('orders');
      },
    },
    { name: 'Indirizzo', Icon: HomeIcon, onClick: () => navigate('address') },
    {
      name: 'Impostazioni',
      Icon: ManageAccountsIcon,
      onClick: () => {},
    },
    {
      name: 'Logout',
      Icon: LogoutIcon,
      onClick: () => {
        navigate('logout');
      },
    },
  ];

  // if isAdmin is true, insert this on top
  if (isAdmin())
    accountInfo.splice(0,0,{
      name: 'Admin',
      Icon: AdminIcon,
      onClick: () => {
        navigate('/Admin');
      },
    });

  return (
    <Container maxWidth="md">
      <Typography variant="h4" color={'gray'}>
        Il mio account
      </Typography>
      <Box
        mt={3}
        flexWrap="wrap"
        display={'flex'}
        justifyContent="space-evenly"
      >
        {accountInfo.map((info, index) => (
          <Box
            onClick={() => info.onClick()}
            key={index}
            border={'3px solid #c2b8b8'}
            m={2}
            borderRadius="5%"
            width={'140px'}
            sx={{
              transition: 'all 300ms',
              ':hover': {
                backgroundColor: 'lightgray',
                cursor: 'pointer',
                transform: 'scale(1.05)',
              },
            }}
          >
            <Stack my={2}>
              <info.Icon
                sx={{
                  fontSize: '50px',
                  color: 'gray',
                  margin: 'auto',
                }}
              />
              <Typography sx={{ margin: 'auto' }} variant="h5" color="gray">
                {info.name}
              </Typography>
            </Stack>
          </Box>
        ))}
      </Box>
    </Container>
  );
};

export default Account;
