import { Container } from '@mui/material';
import React, { useEffect } from 'react';

import myAxios from '../../config/myAxios';
import { getTokenFromLocalStorage } from '../../helper/customer';

const Index = () => {
  useEffect(() => {

    const token =getTokenFromLocalStorage()

    myAxios
      .get('/admin/orders', {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((orders) => {
        console.log(orders);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  return <Container maxWidth='lg'>Riservato admin</Container>
};

export default Index;
