import jwtDecode, { JwtPayload } from 'jwt-decode';

const TOKEN_KEY = 'token';

export interface Customer {
  name: string;
  email: string;
  stripeID: string;
  isAdmin?:boolean
}

type JWTCustomer = JwtPayload & Customer;

/**
 *
 * @param customer
 *
 * get token as parameter, save it in localstorage
 */
export const saveTokenToLocalStorage = (token: string) => {
  localStorage.setItem(TOKEN_KEY, token);
};

export const getTokenFromLocalStorage=()=>{
    return localStorage.getItem(TOKEN_KEY);

}

/**
 * remove token from localStorage
 */
export const logoutLocalStorage = () => {
  localStorage.removeItem(TOKEN_KEY);
};

/**
 *
 * @returns true if token is still valid, false otherwise
 */
export const isAuth = () => {
  const tokenString = localStorage.getItem(TOKEN_KEY);

  if (!tokenString) return false;

  const decodedToken = jwtDecodeWrapper(tokenString)

  const now = new Date();

  if(!decodedToken)
  return false;

  if (decodedToken.exp && decodedToken.exp * 1000 < now.getTime()) {
    logoutLocalStorage();
    return false;
  }

  return true;
};

/**
 * 
 * @returns true if isAdmin, false if not
 */
export const isAdmin = ()=>{
    const tokenString = localStorage.getItem(TOKEN_KEY);

    if (!tokenString) return false;

    const decodedToken = jwtDecodeWrapper(tokenString);

    return !!decodedToken?.isAdmin;
}
/**
 *
 * @returns customer or undefined from localStorage
 */
export const getCustomerFromLocalStorage = () => {
  const tokenString = localStorage.getItem(TOKEN_KEY);

  return getCustomerFromToken(tokenString);
};

/**
 * 
 * @param token 
 * @returns the customer info extracted for a given token
 */
export const getCustomerFromToken = (token: string | null | undefined) => {
  if (!token) return undefined;

  const decodedCustomer = jwtDecodeWrapper(token) ;

  return decodedCustomer;
};

/**
 * 
 * @param token 
 * @returns 
 * wrapper to catch error in case token is currupted
 */
export const jwtDecodeWrapper = (token:string)=>{

  try {
    const decodedCustomer = jwtDecode<JWTCustomer>(token);
    return decodedCustomer
  } catch (error) {
    console.log('Token might be corrupted')
    return undefined;
  }
}
