import { Shipping } from './../pagination/account/address/Address';

export enum OrderStatus {
  IN_LAVORAZIONE = 'In lavorazione',
  SPEDITO = 'Spedito',
  CONSEGNATO = 'Consegnato',
}
export interface OrderI {
  _id: string;
  customerID: string;
  checkoutSessionID: string;
  trackingCode: string;
  orderStatus: OrderStatus;
  orderNum: string;
  amount: number;
  createdAt: Date;
  shippingCost: number;
  shipping: Shipping;
  receipt: string;
}

export interface LineItemI {
  description: string;
  amount_total: number;
  quantity: number;
  price: {
    nickname: string;
    unit_amount: number;
  };
}
