import React, { useEffect, useState } from 'react';
import { Box, Container, Typography } from '@mui/material';

import { useAppSelector } from '../../redux/redux-hooks';
import CartItemView from './CartItemView';

import AddressInfoAndButton from './AddressInfoAndButton';
import ShippingFees from './ShippingFees';
import { Shipping } from '../account/address/Address';
import myAxios from '../../config/myAxios';
import { getTokenFromLocalStorage } from '../../helper/customer';

const Cart = () => {
  const cart = useAppSelector((state) => state.productReducer.cart);

  const [shipping, setShipping] = useState<Shipping|undefined>();
  const [addressLoading, setAddressLoading] = useState(true);
  const [shippingFee,setShippingFee]=useState<number>(0)

  useEffect(() => {
    setAddressLoading(true)
    const token = getTokenFromLocalStorage();

    myAxios
      .get<Shipping>('/customer/address', {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((resp) => {
        setShipping(resp.data);
        setAddressLoading(false)
      })
      .catch(err=>setAddressLoading(false))
      ;
  }, []);

  const numItems = cart.reduce((prev, item) => prev + item.quantity, 0);
  const subTotal = cart.reduce(
    (prev, item) => prev + item.quantity * item.price.unit_amount,
    0
  );
  const total = subTotal+shippingFee;

  const subTotalString = (subTotal / 100).toFixed(2);
const totalString = (total/100).toFixed(2)

  return (
    <Container sx={{ pb: 20 }} maxWidth="md">
      <Typography variant="h4" color={'gray'}>
        Carrello
      </Typography>
      <Box>
        {cart.length === 0 && (
          <Typography color={'gray'} variant="h5">
            Carrello vuoto
          </Typography>
        )}
        {cart.map((cartItem) => (
          <CartItemView key={cartItem.price.id} cartItem={cartItem} />
        ))}
      </Box>

      <Box
        pt={2}
        pr={1}
        display={'flex'}
        justifyContent="flex-end"
        alignItems={'baseline'}
      >
        <Typography pr={2}>
          {numItems} {numItems === 1 ? 'articolo' : 'articoli'}:
        </Typography>
        <Typography variant="h6" fontWeight={'bold'}>
          {subTotalString} €
        </Typography>
      </Box>
      <Box
        pt={2}
        pr={1}
        display={'flex'}
        justifyContent="flex-end"
        alignItems={'baseline'}
      >
        <ShippingFees
          setShippingFee={setShippingFee}
          addressLoading={addressLoading}
          city={shipping?.address?.city}
        />
      </Box>
      <Box
        pt={6}
        my={1}
        pr={1}
        display={'flex'}
        justifyContent="flex-end"
        alignItems={'baseline'}
        sx={{ borderTop: '1px solid gray' }}
      >
        <Typography variant="h4" fontWeight={cart.length === 0 ?'light':'bold'}>
          {cart.length === 0 ? '00.00€' : `${totalString} €`}
        </Typography>
      </Box>
      <AddressInfoAndButton
        cart={cart}
        addressLoading={addressLoading}
        shipping={shipping}
      />
    </Container>
  );
};

export default Cart;
