import { Box, Typography } from '@mui/material';
import React, { useEffect, useState } from 'react';

import myAxios from '../../config/myAxios';

type Props = {
  city?: string;
  addressLoading: boolean;
  setShippingFee: React.Dispatch<React.SetStateAction<number>>;
};

type ShippinfFeesType = {
  display_name: string;
  amount: number;
};

const ShippingFees = ({ city, addressLoading, setShippingFee }: Props) => {
  const [shippingInfo, setShippingInfo] = useState<ShippinfFeesType | null>(
    null
  );

  const amount = ((shippingInfo?.amount || 0) / 100).toFixed(2);
  const amountString = `${amount} €`;

  useEffect(() => {
    if (city)
      myAxios
        .post<ShippinfFeesType>('/stripe/shippingFees', { city })
        .then(({ data }) => {
          setShippingInfo(data);
          setShippingFee(data.amount)
        })
        .catch((err) => console.log(err));
  }, [city,setShippingFee]);

  return (
    <Box display={shippingInfo ? 'flex' : 'none'} alignItems="center">
      <Typography pr={13}>{shippingInfo?.display_name}</Typography>
      <Typography variant="h6" fontWeight={'light'}>
        {amountString}
      </Typography>
    </Box>
  );
};

export default ShippingFees;
