import React, { useState } from 'react';
import {
  Box,
  Card,
  CardActionArea,
  CardContent,
  CardMedia,
  Skeleton,
  Typography,
} from '@mui/material';
import { useSearchParams } from 'react-router-dom';

type Props = {
  id: string;
  title: string;
  url: string;
  price: number;
};

const ItemCard = ({ title, url, price, id }: Props) => {
  const [loading, setLoading] = useState<boolean>(true);
  const [,setSearchPararms]=useSearchParams()
  return (
    <Card
      onClick={() => setSearchPararms({id})}
      sx={{
        maxWidth: { xs: 180, sm: 200 },
        maxHeight: {xs:300,sm:280},
        m: 1,
        borderRadius: 4,
        cursor: 'pointer',
        transition: 'transform .35s',
        ':hover': { transform: 'scale(1.1)' },
      }}
    >
      <CardActionArea>
        <Box sx={{ overflow: 'hidden' }}>
          <Skeleton
            sx={{ bgcolor: 'grey.900', display: loading ? 'block' : 'none' }}
            variant="rectangular"
            width={200}
            height={200}
          />

          <CardMedia
            sx={{
              height: '200px',
              // width: '200px',
              display: !loading ? 'inline' : 'none',

              objectFit: 'cover',
              overflow: 'hidden',
              transition: 'transform .35s ',
              ':hover': {
                transform: 'scale(1.1) rotate(-3deg);',
              },
            }}
            component="img"
            image={url}
            placeholder="/logo.jpg"
            alt="Paella dish"
            onLoad={() => {
              setLoading(false);
            }}
          />
        </Box>
        <CardContent sx={{ p: 1 }}>
          <Typography
            variant="h6"
            sx={{ ':hover': { color: '#c45500' } }}
            color="text.secondary"
          >
            {title}
          </Typography>
          <Typography variant="body1" color="text.secondary">
            A partire da € {price / 100}
          </Typography>
        </CardContent>
      </CardActionArea>
    </Card>
  );
};

export default ItemCard;
