import React from 'react';
import { Box } from '@mui/system';
import { useNavigate } from 'react-router-dom';

import logo from '../../assets/logo.jpg'

const Logo = () => {

  const navigate = useNavigate()
    return (
      <Box

      onClick={()=>navigate('type/Pollame')}
        component="img"
        sx={{
          cursor: 'pointer',
          height: { xs: '60px', sm: '80px' },
        }}
        alt="Logo."
        src={logo}
      />
    );
};

export default Logo;