import React, { useEffect } from 'react';
import { Alert, Container, Typography } from '@mui/material';
import { useAppDispatch, useAppSelector } from '../../../redux/redux-hooks';
import { clearCart } from '../../../redux/reducers/productSlice';

const OrderSuccess = () => {
  const user = useAppSelector((state) => state.customerReducer.customer.name);
  const dispatch = useAppDispatch();

  useEffect(() => {
    dispatch(clearCart());
  }, []);
  return (
    <Container maxWidth="sm">
      <Alert icon={false} variant="standard" severity="success">
        <Typography>
          Ciao <strong>{user}</strong>, Grazie per l'acquisto!
        </Typography>
        <Typography py={2}>Il tuo ordine è stato preso in carico</Typography>
        <Typography>
          Riceverai una nuova mail una volta che l'ordine sarà spedito.
        </Typography>
      </Alert>
    </Container>
  );
};

export default OrderSuccess;
